@tailwind base;
@tailwind components;
@tailwind utilities;

body::-webkit-scrollbar{
  display: none;
}

h1,
h2,
h3,
h4,
p {
  font-weight: bold;
}

#sky {
  background: linear-gradient(0deg, rgb(255, 184, 99) 0%, rgba(233,88,26) 100%);
}

.cloud {
  width: 60%;
  position: fixed;
  margin-left: -50rem;
}

.moon {
  position: absolute;
  width: 25vw;
  height: 25vw;
  margin-left: -10%;
  margin-bottom: -10%;
}

.sun {
  position: absolute;
  width: 16vw;
  height: 16vw;
  margin-left: calc(50% - 5rem);
  margin-top: 10%;
  border-radius: 100%;
  background: radial-gradient(circle, rgb(220,220,100) 0%, rgba(233,173,30) 83%);
}

.desert {
  position: relative;
  width: 100%;
  height: 37%;
  bottom: 0;
  /* background-image: url('./img/desert.png'); */
  background-repeat: no-repeat;
  background-size: cover;
}

.desert_scroll {
  position: relative;
  width: 100%;
  height: 34vw;
  top: 165rem;
  /* background-image: url('./img/desert.png'); */
  background-repeat: no-repeat;
  background-size: cover;
}

.tent {
  position: absolute;
  width: 10rem;
  top: -2rem;
  left: 10%;
}

.startMovie::before {
  content:'';
  position: absolute;
  bottom: 5rem;
  width: 100%;
  height: 10rem;
  background: linear-gradient(0deg, black, transparent);
  z-index: 1000000;
}

.content {
  position: absolute;
  background: #000;
  width: 100vw;
  top: 19rem;
  z-index: 100000;
}

